.Input {
    width: 100%;
    padding: 3px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.Label {
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
}

.InputElement {
    outline: none;
    border: 1px solod #ccc;
    background-color: white;
    font: inherit;
    padding: 6px 0px;
    width:100%;
    display:block;
}

.InputElement:Focus {
    outline: none;
    background-color: #ccc;
}

.Selectlabel {
    color:gray;
    position:relative;
    top:5px;
}

.Select {
    outline: none;
    border: 1px solod #ccc;
    background-color: white;
    font: inherit;
    width:100%;
    display:block;
    position:relative;
    top:3px;
}


